export default {
	props: {
		type: {
			type: String,
			default: ''
		},
		loading: {
			type: Boolean,
			default: false,
		},
		disabled: {
			type: Boolean,
			default: false,
		},
		long:{
			type:Boolean,
			default:false,
		},
		size:{
			type:String,
			default:'normal',
		},
		radius:{
			type:String,
			default:'normal',
		}
	},
	data() {
		return {
			showSlot: true,
		}
	},
	computed: {
		classes(){
			return [
				`ripple`,
				`wa-button`,
				`wa-button-${this.type}`,
				`wa-button-${this.loading}`,
				`wa-button-${this.disabled}`,
				`wa-long-${this.long}`,
				`wa-button-${this.size}`,
				`wa-radius-${this.radius}`,
			]
		}
	},
	methods: {

	},
	mounted() {
	}
}
